//      "counter"
const counters = document.querySelectorAll(".counter");
const animationDuration = 3000;

//   IntersectionObserver    -
const observer = new IntersectionObserver(entries => {
  //   
  entries.forEach(entry => {
    //      
    if (entry.isIntersecting) {
      const counter = entry.target; //  ,    IntersectionObserver

      const countTarget = +counter.getAttribute("data-count");
      const animationStart = Date.now(); //   
      const animationEnd = animationStart + animationDuration; //   

      let countValue = 0; //   
      const updateCounter = () => {
        const timeElapsed = Date.now() - animationStart; //     
        const progress = Math.min(timeElapsed / animationDuration, 1); //    0  1
        const currentValue = Math.floor(countTarget * progress); //   ,      

        counter.innerText = currentValue; //    

        if (timeElapsed < animationDuration) { //  ,     
          const speedFactor = 1 + (100 - countTarget) / 100; //  ,    countTarget
          const interval = Math.max(10, 1000 / (countTarget * speedFactor)); //      countTarget   
          setTimeout(updateCounter, interval); //      
        }
      };

      updateCounter(); //   

      //  observer   ,      
      observer.unobserve(counter);
    }
  });
});

//        observer
counters.forEach(counter => {
  observer.observe(counter);
});
